import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import useTelegram from '../../hooks/useTelegram';
import { privateChanneButtons } from '../../utils/const';
import './PrivateChannel.css';


const PrivateChannel = ({ setPaymentData }) => {
    const navigate = useNavigate();
    const { tg } = useTelegram();

    useEffect(() => {
        tg.BackButton.hide();
        tg.MainButton.hide();
    }, [tg]);
    
    const payment = (e) => {
        setPaymentData(e.target.textContent);
        localStorage.setItem('path', '/private-channel')
        navigate('/payment');
    }

    return (
        <div className='private-channe'>
            <p>  <b>ULTRA PROFIT TRADE</b> ' ,    ,   ,     .    ,    ,  <b>BTC</b>, <b>ETH</b>, <b>DXY</b>   <b>ALTCOINS</b>.   -                .</p>
            <p>,       !</p>
            <p>        :</p>
            <ul>
                <li><b>BTC</b> - ,        BTC</li>
                <li><b>ETH</b> - ,        ETH</li>
                <li><b>ALTCOINS</b> - ,        ALTCOINS</li>
                <li><b>INDEX</b> - ,        DXY, SP500</li>
                <li><b>FOREX</b> - ,        FOREX</li>
                <li><b>SPOT</b> - ,         </li>
                <li><b>FAQ</b> - ,     </li>
                <li><b> </b> - ,          </li>
                <li><b>FLOOD</b> -      </li>
                <li><b> </b> - ,     ,  </li>
                <li><b> </b> - ,     ,  </li>
                <li><b></b> - ,     ,    </li>
                <li><b></b> - ,       </li>
                <li><b>Announcements</b> - ,      '</li>
            </ul>
            <h3></h3>
            {privateChanneButtons.map((button, index) => {
                return(
                    <button key={index} onClick={payment}>{button}</button>
                )
            })}
        </div>
    )
}

export default PrivateChannel;